.button {
    all: unset;
    transition: background-color .2s ease;

    /* color: #666666; */
    background-color: rgba(0, 0, 0, .3);
    /* border: 2px solid #CCCCCC; */

    font-size: 24px;

    display: flex;
    align-items: center;
    justify-content: center;
}

.button--fill-w {
    width: 100%;
}

.button--fill-h {
    height: 100%;
}

.button:active {
    /* background-color: #CCCCCC; */
    background-color: rgba(0, 0, 0, .6) !important;
}

.button:focus {
    outline: none;
}

.button:disabled {
    /* color: #eee; */
    /* background-color: #aaa; */
}
