.dialog-box {
    position: absolute;
    left: 0; right: 0; top: 0; bottom: 0;
    max-width: 600px;
    margin: auto;
}

.dialog-box {
    background: linear-gradient(135deg, #000f4b, #540037);
    background-size: 400% 400%;

    -webkit-animation: background 30s ease infinite;
    -moz-animation: background 30s ease infinite;
    animation: background 30s ease infinite;
}

@-webkit-keyframes background {
    0%{background-position:0% 4%}
    50%{background-position:100% 97%}
    100%{background-position:0% 4%}
}
@-moz-keyframes background {
    0%{background-position:0% 4%}
    50%{background-position:100% 97%}
    100%{background-position:0% 4%}
}
@keyframes background {
    0%{background-position:0% 4%}
    50%{background-position:100% 97%}
    100%{background-position:0% 4%}
}

@keyframes fade-opacity {
    from {
        opacity: 0;
        transform: scale(.5);
    }
    to {
        opacity: 1;
        transform: scale(1);
    }
}

.dialog-box--opening {
    animation: fade-opacity 200ms ease forwards;
}

.dialog-box--closing {
    animation: fade-opacity 200ms ease reverse forwards;
}

.dialog-box--opened {
    opacity: 1;
}

.dialog-box--closed {
    opacity: 0;
}

.dialog-box__header {
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    background-color: rgba(0, 0, 0, .2);
    /* background-color: #F3F5FB; */
}

.dialog-box__content {
    height: calc(100dvh - 60px);
}

.dialog-box__close-button-container {
    width: 60px;
    height: 60px;
}
