.text-input {
    all: unset;
    box-sizing: border-box;
    background-color: rgba(0, 0, 0, .4);
    font-size: 22px;
    font-style: italic;
    padding: 10px 15px;
}

.text-input::placeholder {
    opacity: 1;
    transition: opacity .2s ease;
}

.text-input:focus.text-input::placeholder {
    opacity: 0;
}

.text-input--fill-w {
    width: 100%;
}

.text-input--fill-h {
    height: 100%;
}
