.app {
    height: 100dvh;
    max-width: 600px;
    margin: auto;

    background-color: var(--theme-main-bg-color);
    color: var(--theme-main-fg-color);

    background: rgb(8,31,102);
    background: linear-gradient(90deg, rgba(8,31,102,1) 0%, rgba(20,54,130,1) 100%);
}

.app {
    background: linear-gradient(135deg, #000f4b, #540037);
    background-size: 400% 400%;

    -webkit-animation: background 60s ease infinite;
    -moz-animation: background 60s ease infinite;
    animation: background 60s ease infinite;
}

@-webkit-keyframes background {
    0%{background-position:0% 4%}
    50%{background-position:100% 97%}
    100%{background-position:0% 4%}
}
@-moz-keyframes background {
    0%{background-position:0% 4%}
    50%{background-position:100% 97%}
    100%{background-position:0% 4%}
}
@keyframes background {
    0%{background-position:0% 4%}
    50%{background-position:100% 97%}
    100%{background-position:0% 4%}
}

.app--theme-light {
    --theme-main-bg-color: #FFFFFF;
    --theme-main-fg-color: #000000;
    --theme-accent-color:  #DDDDDD;
}

.app--theme-dark {
    --theme-main-bg-color: #1C1933;
    --theme-main-fg-color: #a9c5fa;
}

.app__view-container {
    height: calc(100% - 60px);
    overflow: hidden;
}

.app__nav-container {
    height: 60px;
}
