.food-entries-view {
    height: 100%;
}

.food-entries-view__food-entries-container {
    height: 100%;
}

.food-entries {
    height: 100%;
}

.food-entries__header-container {
    /* background-color: #F3F5FC; */
    background-color: rgba(0, 0, 0, 0.4);
    
    padding: 0 10px;
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 20px;
    font-weight: 500;
    /* color: #617082; */
}

.food-entries__calories-total {
    font-size: 22px;
    /* font-style: italic; */
    font-weight: 500;
    margin-left: 5px;
    user-select: none;
}

.food-entries__date {
    font-size: 16px;
    width: 125px;
    height: 35px;
    background-color: rgba(0, 0, 0, .2);
    color: var(--theme-main-fg-color) !important;
    border: none;
}

.food-entries__food-entries-container {
    /* border: 2px solid #F2F5FC; */
    height: calc(100% - 60px);
    overflow: scroll;
}

.food-entries__no-entries-message {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 80px;
    /* color: #EEE; */
}

:root {
    --food-entry--height: 60px;
    --food-entry--base-font-size: 18px;
    --food-entry--border-color: #F2F5FC;
}

.food-entry {
    user-select: none;
    height: var(--food-entry--height);
    /* border-bottom: 2px solid var(--food-entry--border-color); */
    display: flex;
    align-items: center;
    justify-content: space-between;
    /* color: #617082; */
    transition: background-color .2s ease;
    overflow: hidden;
    font-size: 18px;
    /* padding: 0 20px; */
}

.food-entry:nth-child(even) {
    background-color: rgba(0, 0, 0, .2);
}

.food-entry:nth-child(odd) {
    background-color: rgba(0, 0, 0, .1);
}

.food-entry:last-child {
    border-bottom: none;
}

.food-entry:active {
    background-color: rgba(0, 0, 0, .4);
}

.food-entry__time-stamp {
    opacity: .4;
    transition: opacity .2s ease;
}

.food-entry:active .food-entry__time-stamp {
    opacity: 1;
}

.food-entry__description {
    font-weight: 400;
    font-size: 18px;
    font-style: italic;
    flex: 1;
    overflow: hidden;
    text-overflow: ellipsis;
    margin-left: 20px;
}

.food-entry__time-stamp {
    text-align: center;
    width: 60px;
    height: 100%;
    display: flex;
    /* flex-direction: column; */
    align-items: center;
    justify-content: center;
    font-size: 18px;
    margin-right: 10px;
    /* border-left: 2px solid var(--food-entry--border-color); */
}

.food-entry__time-stamp-time {
    font-weight: 400;
    font-style: italic;
    opacity: .6;
    margin-right: 4px;
}

.food-entry__time-stamp-ampm {
    font-size: 10px;
    font-weight: 400;
    font-style: italic;
    opacity: .4;
}

.food-entry__calories {
    width: 60px;
    /* background-color: orange; */
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 20px;
    font-weight: 400;
    font-style: italic;
    opacity: .8;
    /* border-right: 2px solid var(--food-entry--border-color); */
    margin-right: 10px;
}
